import uniIcon from '@/components/i-icon/i-icon.vue'
import uniShader from '@/components/uni-shader.vue'
export default {
	components: {
		uniIcon,
		uniShader
	},
	props: {
		title: {
			type: String,
			default: '余额提现'
		}
	},
	data() {
		return {
			shader: false,
			password: [],
			items: [0, 1, 2, 3, 4, 5],
			pattern: 'hidden'
		};
	},
	methods: {
		show() {
			this.shader = true;
			let args = {
				cancel: false
			};
			this.$emit('shown', args);
			if (!args.cancel) {
				this.pattern = 'slideup';
			}
		},
		hide() {
			this.shader = false;
			let args = {
				cancel: false
			};
			this.$emit('hidden', args);
			if (!args.cancel) {
				this.pattern = 'slidedown';
			}
		},
		inputPwd(e) {
			let char = e.currentTarget.dataset.char;
			let args = {
				target: this,
				data: char,
				cancel: false,
				callback: undefined
			};
			this.$emit('input', args);
			if (!args.cancel) {
				if (this.password.length === this.items.length) {
					return;
				}
				if (args.callback && typeof args.callback === 'function') {
					args.callback.call(this);
				}
				this.password.push(char);
				if (this.password.length === this.items.length) {
					//长度达6位，自动验证
					this.$emit('completed', this.password.join(''));
					return;
				}
			}
		},
		backspace() {
			let args = {
				cancel: false
			};
			this.$emit('delete', args);
			if (!args.cancel) {
				if (this.password.length > 0) {
					this.password = this.password.slice(0, this.password.length - 1);
				}
			}
		}
	}
}
